import Common from '~/plugins/common.js'
import SendSentence from '~/components/shared/SendSentence.vue'
import InfiniteLoading from 'vue-infinite-loading'
export default {
  components:{
    SendSentence,
    InfiniteLoading
  },
  data() {
    return{
      MICROCMS_KEY: '',
      qWord: '',
      posts: [],
      modeQuestion: 'question',
      modeReply: 'reply',
      canvas: null,
      ctx: null,
      image: null,
      postCount: 5,
      state: null,
      title: ''
    }
  },
  methods:{
    transition(id) {
      this.$router.push({ path: id })
    },
    async loadNewPost($state) {
      this.state = $state
      const loadPostNumber = 10
      await this.$axios.$get('https://q-box.microcms.io/api/v1/q_box_posts?filters=answer[exists][and]question[contains]' + this.qWord + '&limit=' + loadPostNumber +'&offset=' + this.postCount, {
        headers: { 'X-MICROCMS-API-KEY': this.MICROCMS_KEY}
      }).then((response) => {
        if(this.postCount < response.totalCount) {
          Common.modifyUrlInPost(response.contents, 'answer')
          this.posts = this.posts.concat(response.contents)
          Common.generateImage(document, response.contents, 'question', '1')
          this.setReply()
          this.postCount += response.contents.length
          $state.loaded()
        }else if(this.postCount >= response.totalCount){
          this.postCount = 1
          $state.complete()
        }
      }).catch((error) => {
        $state.error()
        alert('通信に失敗しました。：' + error)
        console.log(error)
      })
    },
    async getPost(word) {
      this.qWord = word
      if(this.state) {
        this.state.reset()
      }
      this.setTitle(word)
      await this.$axios.$get('https://q-box.microcms.io/api/v1/q_box_posts?filters=answer[exists][and]question[contains]' + word + '&limit=5', {
        headers: { 'X-MICROCMS-API-KEY': this.MICROCMS_KEY}
      }).then((response) => {
        this.posts = response.contents
        Common.generateImage(document,response.contents, 'question', '1')
        this.setReply()
        Common.modifyUrlInPost(this.posts, 'answer')
      })
    },
    setTitle(word) {
      this.title = word
    },
    async setReply() {
      if(this.posts) {
        for(const post of this.posts) {
          await this.$axios.$get('https://q-box.microcms.io/api/v1/q_box_replies?filters=replyFor[equals]' + post.id + '[and]replyAnswer[exists]&orders=createdAt',{
            headers: { 'X-MICROCMS-API-KEY': this.MICROCMS_KEY}
          }).then((response) => {
            Common.generateImage(document, response.contents, 'replySentence', '1')
            this.$set(post, 'replies', response.contents)
            Common.modifyUrlInPost(post.replies, 'replyAnswer')
          }).catch((error) => {
            console.log(error)
          })
        }
      }
    },
  },
  mounted() {
    this.MICROCMS_KEY = process.env.MICROCMS_KEY
  }
}
